.main-navigation {
	display: block;
	width: 100%;

	ul {
		display: none;
		list-style: none;
		margin: 0;
		padding-left: 0;

		ul {
			box-shadow: 0 3px 3px rgba(0, 0, 0, 0.2);
			float: left;
			position: absolute;
			top: 100%;
			left: -999em;
			z-index: 99999;

			ul {
				left: -999em;
				top: 0;
			}

			li {

				&:hover > ul,
				&.focus > ul {
					display: block;
					left: auto;
				}
			}

			a {
				width: 200px;
			}

		}

	}

	li {
		position: relative;

	}

	a {
		display: block;
		text-decoration: none;
	}

	.current_page_item > a,
	.current-menu-item > a,
	.current_page_ancestor > a,
	.current-menu-ancestor > a {
		background-color: #335aaf;
	}
}

/* Small menu. */
.menu-toggle,
.main-navigation.toggled ul {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	border: none;
	margin: 0;
	color: white;
	z-index: 100;


	svg {
		display: block;
		max-width: 100%;
		height: 2rem;
		width: 2rem;
		fill: white;
	}

	li a {
		//make the menu items full width
		width: 100%;
		text-align: center;
		padding: 1rem;
		display: block;
		z-index: 100;
		background-color: #1f1f1f;;
		display: block;

	}

}



button.menu-toggle {
	margin: 0;
	margin-right: 0;
	padding: 0;
	background-color: #1f1f1f;
	margin-left: auto;
	padding: .75rem;
}

.main-navigation.toggled ul {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	width: 100%;
	z-index: 100;

	li {
		width: 100%;
		background-color: #1f1f1f;
		display: block;
	}

	a {
		background-color: #1f1f1f;;
		display: block;
		width: 100%;
	}

	.current_page_item > a,
	.current-menu-item > a,
	.current_page_ancestor > a,
	.current-menu-ancestor > a {
		background-color: #335aaf;
	}
}


@media screen and (min-width: 37.5em) {

	.menu-toggle {
		display: none;
	}

	.main-navigation ul {
		display: flex;
		justify-content: flex-end;

	}
}

.comment-navigation,
.posts-navigation,
.post-navigation {

	.site-main & {
		margin: 0 0 1.5em;
	}

	.nav-links {
		display: flex;
	}

	.nav-previous {
		flex: 1 0 50%;
	}

	.nav-next {
		text-align: end;
		flex: 1 0 50%;
	}
}

.site-branding {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	margin: 0;
	padding: 0;

	p.site-title {

		margin: 0;
		padding: 0;

		a {
			font-size: 1.5rem;
			display: block;
			margin: 0;
			padding: .6rem 1rem;
			text-decoration: none;

			&:hover {
				background-color: #335aaf;
			}
		}


	}

	.site-description {
		font-size: 1.125em;
		font-weight: 400;
		margin: 0;
	}
}

header.site-header {
	background-color: #1f1f1f;;
	text-transform: uppercase;
	border-top: 5px solid #335aaf;
	height: 3.8rem;
	display: flex;
	justify-content: space-between;

	@media (min-width: 600px){


	}

	a {
		color: white;
		text-decoration: none;
		font-weight: 700;
		display: block;
		height: 100%;
		width: 100%;
	}

	li a{
		padding: 1rem;

		&:hover {
			background-color: #335aaf;
		}
	}
}
